*{
    margin: 0;
    padding: 0;
}
body{
    height: 100vh;
    background:linear-gradient( #2a00B7,#42006c);
}
.container{
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 20px;
    width: 600px;
    background: #fff;
    padding-bottom:30px;
    

}
.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    width: 100%;
    gap: 9px;
}
.text{
    color: #3c2f5a;
    font-size: 48px;
    font-weight: 700;
}
.underline{
    width: 61px;
    height: 6px;
    background-color: #2a00B7;
    border-radius: 9px;
}
.inputs{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background: #eaeaea;
    border-radius:6px ;
}
.input i{
    margin: 0px 30px;
}
.input input{
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;

}
.forgot-password{
    padding-left: 62px;
    margin-top: 27px;
    color: #797979;
    font-size: 18px;

}
.forgot-password span{
    color: #42006c;
    cursor: pointer;

}
.submit-container{
    display: flex;
    gap: 30px;
    margin: 15px auto;

}
.submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background: #4d2764;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;

}
.gray{
    background: #EAEAEA;
    color: #676767;
}